
.header_outer_container {
    position: sticky;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(148, 132, 132);
    background-color: rgb(20, 20, 20);
    background-color: transparent;
    transition-timing-function: ease-in;
   
    height: 10vh;
    color: white;
    transition: all 0.5s ease-in; 
    z-index: 1;
} 
.header_outer_container:hover {
    transform: scale(1.1);
}

.header_container {
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header_left ul {
    display: flex;
    align-items: center;
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.header_left ul li {
    margin: 0 15px; 
    cursor: pointer;
}

.header_right ul {
    display: flex;
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.header_right ul li {
    margin: 0 10px; /* Adjust spacing as needed */
    cursor: pointer;
}